___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-size: 25px;
}

body {
  height: 100vh;
  width: 100vw;
  background-color: #85ffbd;
  background-image: linear-gradient(45deg, #85ffbd 0%, #fffb7d 100%);
}

form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 5px;
  border-radius: 5px;
  box-shadow: 1px 1px 12px 0px rgba(0, 0, 0, 0.75);
  width: max-content;
  padding: 10px;
  margin: 50px auto;
}

label {
  font-weight: bold;
}

input {
  border: none;
  border-radius: 5px;
}

select {
  border: none;
  border-radius: 5px;
}

form > div {
  display: flex;
  flex-direction: column;
  width: min-content;
}

form div:last-child {
  flex-direction: row;
  gap: 5px;
}

form div:last-child button {
  padding: 2px 5px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

form > div > div {
  display: flex;
  min-width: max-content;
  gap: 5px;
}

#cancelBtn:hover {
  background-color: rgb(255, 109, 109);
}

#confirmBtn:hover {
  background-color: rgb(110, 189, 110);
}

.error-msg {
  display: flex;
  flex-wrap: wrap;
  font-size: 0.6rem;
  background: linear-gradient(to right, #fe8c00, #f83600);
  color: white;
  padding: 2px;
  border-radius: 5px;
  margin-top: 3px;
}

.display-none {
  display: none;
}

.information-header {
  visibility: hidden;
  background: linear-gradient(to right, #fe8c00, #f83600);
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.75);
  color: white;
  padding: 10px 50px;
  font-size: 1.5rem;
  height: 70px;
}

.visible {
  visibility: visible;
}
